
@import url("https://fonts.googleapis.com/css?family=Didact+Gothic");
html{font-size:16px
}
div,a,p,strong,label,input,select,textarea,td,th,button,li,code{font-size:1rem
}
h1,h2,h3,h4,h5,h6,div,span,a,strong,p,label,select,textarea,td,th,li,input,input::-webkit-input-placeholder,button,code{font-family:"Didact Gothic","Microsoft JhengHei",monospace,"Fontawesome"
}
h1,h2,h3,h4,h5,h6,div,span,a,strong,p,label,select,textarea{color:#575757
}
.form-control{-webkit-border-radius:0;-moz-border-radius:0;border-radius:0
}
.table th,.table td{font-size:14px
}
.table .thead-inverse{background-color:#4a4a4a;color:white
}
.text-info{color:#5bc0de
}
.text-primary{color:#0275d8
}
.text-success{color:#80c780
}
.text-warning{color:#f0ad4e
}
.text-danger{color:#d9534f
}
.btn{outline:none;cursor:pointer;-webkit-border-radius:0;-moz-border-radius:0;border-radius:0
}
.btn:focus,.btn:active{outline:none
}
.btn>span{color:white
}
a[href]{color:#31b0d5
}
a.label.label-info{background-color:#5bc0de;color:white;font-size:14px;padding:0.25rem 0.5rem 0.25rem;margin-right:10px;font-weight:normal;font-family:"Didact Gothic","Microsoft JhengHei",monospace,"Fontawesome"
}
div[role="tooltip"]{z-index:600
}
span[data-role="mark"]{font-weight:bold;color:#80c780
}
.btn-transparent{background-color:rgba(255,255,255,0);border:1px white solid;color:white
}
.radio-group{display:inline-block
}
.radio-group input[type="radio"]{display:none
}
.radio-group input[type="radio"]:checked+label:before{background-color:#80c780
}
.radio-group.orange input[type="radio"]:checked+label:before{background-color:#f2b866
}
.radio-group label{font-weight:normal;cursor:pointer;vertical-align:middle;margin-right:.5em;padding-top:5px
}
.radio-group label:before{content:'';-webkit-border-radius:50%;-moz-border-radius:50%;border-radius:50%;width:1em;height:1em;display:inline-block;background:white;-webkit-box-shadow:inset 0 0 0 3px #ccc;-moz-box-shadow:inset 0 0 0 3px #ccc;box-shadow:inset 0 0 0 3px #ccc;vertical-align:middle;margin-right:0.2em
}
.form-checkbox input[type="checkbox"]{display:none
}
.form-checkbox input[type="checkbox"]:checked+label:before{background-color:#303030
}
.form-checkbox input[type="checkbox"]+label{font-weight:normal;font-size:14px;cursor:pointer;vertical-align:middle;margin-right:.5em;padding-top:5px
}
.form-checkbox input[type="checkbox"]+label:before{content:'';width:1em;height:1em;display:inline-block;background:white;-webkit-box-shadow:inset 0 0 0 3px #ccc;-moz-box-shadow:inset 0 0 0 3px #ccc;box-shadow:inset 0 0 0 3px #ccc;vertical-align:middle;margin-right:0.5em
}
.lock-icon{color:#f0ad4e
}
button.btn.btn-main{color:white;background-color:#303030;-webkit-box-shadow:none;-moz-box-shadow:none;box-shadow:none;outline:none;border:none;padding:5px 15px
}
button.btn.btn-main.square{padding:10px
}
button.btn.btn-main:active{background-color:#4a4a4a
}
button.btn.btn-main:active,button.btn.btn-main:focus{outline:none
}
@media (min-width: 992px){
div[data-role="form"]{margin:10px 0
}
div[data-role="form"].row{padding:0
}
div[data-role="form"] div[data-role="form-item"]{margin:10px 0
}
div[data-role="form"] div[data-role="form-item"] label[data-role="item-label"]{margin:0;vertical-align:bottom;height:37px;border-bottom:1px #303030 solid
}
div[data-role="form"] div[data-role="form-item"] div[data-role="item-control"]{height:37px;border-bottom:1px #b0b0b0 solid
}
div[data-role="form"] div[data-role="form-item"].textarea{height:150px
}
div[data-role="form"] div[data-role="form-item"].textarea label[data-role="item-label"]{height:150px
}
div[data-role="form"] div[data-role="form-item"].textarea div[data-role="item-control"]{height:150px
}
div[data-role="form"] div[data-role="form-item"] input{border:none;-webkit-box-shadow:none;-moz-box-shadow:none;box-shadow:none;border-bottom:1px #b0b0b0 solid
}
div[data-role="form"] div[data-role="form-item"] textarea{border:none;-webkit-box-shadow:none;-moz-box-shadow:none;box-shadow:none;border-bottom:1px #b0b0b0 solid;resize:none;height:150px
}
}
.disc-list li{list-style:disc
}
body{min-height:100vh
}
.loading-enter-active,.loading-leave-active{-webkit-transition:all 0.5s ease;-moz-transition:all 0.5s ease;transition:all 0.5s ease
}
.loading-enter,.loading-leave-active{opacity:0
}
.router-enter-active,.router-leave-active{-webkit-transition:all 0.5s ease;-moz-transition:all 0.5s ease;transition:all 0.5s ease
}
.router-enter,.router-leave-active{opacity:0
}
.page-header{margin-top:20px
}
.page-body{min-height:100vh
}



